const emit = defineEmits<{
    /**
     * Fired on the owning Scheduler/Gantt after a dependency drag creation operation finished, no matter to outcome
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {TimeSpan} params.event.source The source task
     * @param {TimeSpan} params.event.target The target task
     * @param {DependencyBaseModel} params.event.dependency The created dependency
     */
    (e: 'afterDependencyCreateDrop', params: ((event: { source: TimeSpan, target: TimeSpan, dependency: DependencyBaseModel }) => void)|string): void
    /**
     * Fires on the owning Scheduler or Gantt widget after a dependency is successfully saved using the
     * dependency edit popup
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {Scheduler} params.event.source The scheduler instance or Gantt instance
     * @param {SchedulerDependencyModel} params.event.dependencyRecord The dependency about to be saved
     */
    (e: 'afterDependencySave', params: ((event: { source: Scheduler, dependencyRecord: SchedulerDependencyModel }) => void)|string): void
    /**
     * Fires on the owning Scheduler at the end of the drag create gesture whether or not
     * a new event was created by the gesture.
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {Scheduler} params.event.source
     * @param {SchedulerEventModel} params.event.eventRecord The event record being created
     * @param {SchedulerResourceModel} params.event.resourceRecord The resource record
     * @param {HTMLElement} params.event.eventElement The element representing the created event record
     */
    (e: 'afterDragCreate', params: ((event: { source: Scheduler, eventRecord: SchedulerEventModel, resourceRecord: SchedulerResourceModel, eventElement: HTMLElement }) => void)|string): void
    /**
     * Fired on the owning Scheduler after event drop
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {Scheduler} params.event.source
     * @param {SchedulerAssignmentModel[]} params.event.assignmentRecords
     * @param {SchedulerEventModel[]} params.event.eventRecords
     * @param {boolean} params.event.valid
     * @param {object} params.event.context
     * @param {MouseEvent} params.event.domEvent Browser event
     */
    (e: 'afterEventDrop', params: ((event: { source: Scheduler, assignmentRecords: SchedulerAssignmentModel[], eventRecords: SchedulerEventModel[], valid: boolean, context: object, domEvent: MouseEvent }) => void)|string): void
    /**
     * Fires on the owning Scheduler after editor is closed by any action - save, delete or cancel
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {Scheduler} params.event.source The scheduler
     */
    (e: 'afterEventEdit', params: ((event: { source: Scheduler }) => void)|string): void
    /**
     * Fires on the owning Scheduler after an event is successfully saved
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {Scheduler} params.event.source The scheduler instance
     * @param {SchedulerEventModel} params.event.eventRecord The record about to be saved
     */
    (e: 'afterEventSave', params: ((event: { source: Scheduler, eventRecord: SchedulerEventModel }) => void)|string): void
    /**
     * Fired any time there is a change to the assignments selected in the Scheduler.
     * @param {string} e Event name
     * @param {object} params Event parameters
     * @param {object} params.event Event object
     * @param {'select'|'deselect'|'update'|'clear'} params.event.action One of the actions 'select', 'deselect', 'update', 'clear'
